import { Component, OnInit } from '@angular/core';
import { Item } from '../Item'
import { DataService } from '../data.service'

@Component({
  selector: 'app-shopping-item',
  templateUrl: './shopping-item.component.html',
  styleUrls: ['./shopping-item.component.css'],
  providers: [DataService]
})
export class ShoppingItemComponent implements OnInit {
  items: Item[] = []
  selectedItem: Item
  toggleEditForm: boolean = false

  constructor(private dataService:DataService) { }

  getItems() {
    this.dataService.getShoppingItems()
      .subscribe(items => {
        this.items = items
      })
  }

  addItem(form) {
    let newItem: Item = form.value
        newItem.itemBought = false

    this.dataService.addShoppingItem(newItem)
      .subscribe(item => {
        this.items.push(item)
      })

    form.reset()
  }

  showEditForm(item) {
    this.selectedItem = item
    this.toggleEditForm = !this.toggleEditForm
  }

  editItem(form) {
    let updatedItem: Item = {
      _id: this.selectedItem._id,
      itemName: form.value.itemName,
      itemQuantity: form.value.itemQuantity,
      itemBought: this.selectedItem.itemBought
    }

    this.dataService.updateShoppingItem(updatedItem)
      .subscribe((item => {
        var index = this.items.findIndex(item => item._id == updatedItem._id)
        this.items[index] = updatedItem
      }))

    this.toggleEditForm = !this.toggleEditForm
  }

  updateItemBought(updatedItem) {
    updatedItem.itemBought = !updatedItem.itemBought

    this.dataService.updateShoppingItem(updatedItem)
      .subscribe((item => {
        var index = this.items.findIndex(item => item._id == updatedItem._id)
        this.items[index] = updatedItem
      }))
  }

  deleteItem(id) {
    this.dataService.deleteShoppingItem(id)
      .subscribe(data => {
        if(data.n == 1) {
          for (var i = 0; i < this.items.length; i++) {
            if(id == this.items[i]._id) {
              this.items.splice(i, 1)
            }
          }
        }
      })
  }

  ngOnInit() {
    this.getItems()
  }

}
